/* General layout */

.App {
    text-align: center;
    min-height: 100%;
}

.App {
    display: grid;
    grid-template-areas:
        'header'
        ' main '
        'footer';
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 100%;
}

header {
    grid-area: header;
}
main {
    grid-area: main;
}
footer {
    grid-area: footer;
}

main {
    /*flex-grow: 1;*/

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

header,
main,
footer {
    /*flex-shrink: 0;*/
}

/* Buttons! */

/* Remove the outline that appears when you tap buttons on an Android phone */
* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  outline: none !important;
}

button {
    box-sizing: border-box;

    background: var(--btn-bg);
    border: none;
    border-radius: 0.5em;
    margin: 0.2em;
    text-align: center;

    font-size: var(--btn-text-normal);

    height: var(--btn-size);
    padding: calc(var(--btn-padding) * 0.5) calc(var(--btn-padding) * 2);
}

.btn-wide {
    min-width: 13em;
}

button:hover {
    background: var(--btn-hover-bg);
}

button:focus {
    border: var(--btn-border-size) solid var(--border-color);
}

button:disabled {
    background: var(--btn-disabled-bg);
    color: var(--btn-disabled-fg);
}

button.active {
    background: var(--btn-active-bg);
}

button.active:hover {
    background: var(--btn-active-hover-bg);

}
